.common-swiper{
    position: relative;
}
.common-swiper:hover .btn .leftBtn{
    visibility:visible;
}
.common-swiper:hover .btn .rightBtn{
    visibility:visible;
}

/* left */
.common-swiper .btn .leftBtn{
    visibility: hidden;
    z-index: 10;
    position: absolute;
    left: 0px;
    top: 50%;
    transform: translateY(-50%);
    width: 30px;
    height:50px;
}

.common-swiper .btn .leftBtn img{
    cursor: pointer;
    width: 100%;
    transform: rotate(90deg);
}
/* right */
.common-swiper .btn .rightBtn{
    visibility: hidden;
    z-index: 10;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 0px;
    width: 30px;
    height:50px;
}

.common-swiper .btn .rightBtn img{
    cursor: pointer;
    width: 100%;
    transform: rotate(-90deg);
}




